.sidebar{background-color:#fff;transition:.35s ease;border-right:.75px solid;width:225px}.sidebar.mv{position:absolute;z-index:1000}.sidebar.mv::after{position:absolute;transition:.35s ease;z-index:10;content:"";top:0px;left:235px;width:100vw;height:100vh}.sidebar>.content>.items{transition:.285s ease;padding:12px 16px;cursor:pointer}.sidebar>.content>.items.active{background-color:rgba(110,163,243,.65) !important}.sidebar.collapsed{width:70px}.sidebar.collapsed.mv{width:0px;left:-26px;position:absolute}.sidebar.collapsed.mv::before{position:absolute;z-index:10;content:"";top:0px;left:-100%;width:0px;height:100vh}.sidebar.collapsed>.header{justify-content:center}.sidebar.collapsed>.content>.items{transition:.285s ease;justify-content:center;padding:12px 0px}.navigation-toogle{border:2px solid;position:absolute;right:-12px;padding:.275em;border-radius:100%;display:flex;flex-direction:row;justify-items:center;justify-content:center;width:25px;height:25px;cursor:pointer}.navigation-toogle>*{transition:.75s ease;transform:rotate(180deg)}.navigation-toogle.collapsed>*{transform:rotate(0deg) !important}